.container-fluid {
  display: flex;
  gap: 30px;
}
.scrip_header {
  background: #e11d48;
}
.scrip__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.scrip__title-scrip {
  display: flex;
  align-items: center;
  gap: 10px;
}
.scrip__title-scrip h1 {
  font-size: 27px;
  color: white;
  font-family: "Noto Sans JP", sans-serif;
}
.scrip__title_right {
  display: flex;
  align-items: center;
  gap: 10px;
}
.scrip__title_right h3 {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 18px;
color: white;
}
.ant-col-16 {
  max-width: unset;
}
.paragraph__list {
  width: 400px;
}
.paragraph__list__span {
  border: 1px solid #edecec;
  width: 100%;
}
.paragraph__title {
  margin-bottom: 35px;
}
.paragraph__title h2 {
  font-size: 28px;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 600;
  margin-block-start: unset;
  margin-top: 15px;
}
.topic {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 15px;
  margin-block-end: 10px;
  font-weight: 400;
}
.keywords {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 15px;
  margin-block-end: 10px;
  font-weight: 400;
}
.keywords span {
  font-size: 12px;
  font-family: "Noto Sans JP", sans-serif;
  margin-block-end: 10px;
  font-weight: 400;
}
.select__option {
  display: flex;
  gap: 20px;
  align-items: center;
}
.ant-select-selector {
  padding: unset;
  padding-left: 10px !important;
  padding-right: 50px !important;
}
.center__tab {
  max-width: 1050px;
  height: 700px;
  padding: 20px;
  background-color: #fff;
  margin-top: 15px;
  border: 1px solid #edeef1;
  overflow:auto;  
  border-radius: 15px;
}

.center__tab_input{
  width: 1050px;
  min-width: 100%;
  max-width: 100%;
}



@media screen and (max-width: 1280px) {
  .center__tab {
    height: 500px; 
  }
}




.center__title {
  display: flex;
  align-items: center;
  gap: 10px;
}
.center__title p {
  margin-bottom: unset;
}
.entry {
  font-size: 18px;
  font-family: "Noto Sans JP", sans-serif;
  overflow: hidden; 
  /* border-right: 2px solid #000;  */
  padding-right: 8px; 
  animation: blink-caret 0.75s step-end infinite;
}


@keyframes blink-caret {
  from,
  to {
      border-color: transparent;
  }
  60% {
      border-color: #000;
  }
}
.ant-card-head {
  border-bottom: unset !important;
  padding: 15px 12px !important;
}
.right__tab{
  margin-top: 15px;
  height: 800px;
  width: 500px;
  overflow: auto;
}
.right__tab_card{
  width: 40px;
  display: flex;
  height: 40px;
  background-color: #bbf7d0;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.right__money{
  font-size: 1.5rem;
  font-weight: 700;
}
.card__tab{
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 10px;
}
.list__tools_text h6{
  font-family: "Noto Sans JP", sans-serif;
  font-size: 18px;
  margin-block-end: unset;
  font-weight: 700;
}
.list__tools_text span{
  font-family: "Noto Sans JP", sans-serif;
  font-size: 12px;
  color: #ee7d95;
  
}
.list__tools_text div{
  border: 1px solid #edecec;
  margin-top: 20px;
}
.socialmedia__ai h6{
  font-family: "Noto Sans JP", sans-serif;
  font-size: 15px;
  margin-block-end: unset;
  font-weight: 700;
}
.socialmedia__ai span{
  font-family: "Noto Sans JP", sans-serif;
  font-size: 12px;
  color: #ee7d95;
}
.socialmedia__ai ul{
  margin-block-start: unset;
  margin-block-end: unset;
}
.clear{
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 20px;
  margin-top: 20px;
}
.border__all{
  border: 1px solid #edecec;
}
.footer__card{
  width: 40px;
  display: flex;
  height: 40px;
  background-color: #ffe4e5;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.footer__text{
  margin-top: 20px;
  font-size: 17px;
  font-weight: 800;
  font-family: "Noto Sans JP", sans-serif;
}
.footer__span{
  margin-top: 20px;
  font-size: 14px;
  font-weight: 400;
  font-family: "Noto Sans JP", sans-serif;
}
.about{
  margin-top: 40px;
  text-align: center;
}
.about span{
  font-size: 15px;
  font-weight: 700;
  font-family: "Noto Sans JP", sans-serif;
}
.footer__section{
  margin-bottom: 20px;
}